.container {
  position: relative;
  background-color: rgb(252, 221, 165);
}

.container::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url('../../../source/image/bean_pattern.png');
  background-repeat: repeat;
  background-size: 830px;
  opacity: 0.2;
}

.h1 {
  font-size: 1.6rem;
  color: rgb(0, 0, 0);
  text-align: left;
  padding-left: 20px;
  font-weight: 600;
  padding-top: 0px;
}

.h3 {
  font-size: 1.2rem;
  color: rgb(0, 0, 0);
  text-align: left;
  padding-left: 20px;
  font-weight: 600;
}

.h2 {
  font-size: 3rem;
  margin-top: 35px;
}

.p {
  font-size: 1.2rem;
  text-align: left;
  line-height: 1.4;
}

@keyframes dissolve {
  from {
      opacity: 0;
  }

  to {
      opacity: 1;
  }
}

.container2 {
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  animation: dissolve 2s ease-in-out;
  margin-top: 50px;
  
}

.container2 img {
  width: 200px;
  height: 400px;
  display: flex;
}

.container3 {
  margin-left: 50px;
  margin-bottom: 70px;
}

.point {
  font-size: 1rem;
  flex-direction: column;
  display: flex;
  gap: 3px;
}


.card{
  position: relative;
}

.tooltip {
  display: none; /*기존에는 툴팁 숨김 */
  position: absolute;
  width: 200px;
  height: auto;
  padding: 10px;
  bottom: 100%; 
  left: 50%;
  transform: translateX(-66%);
  border-radius: 10px;
  background: #313030;
  color: #fff;
  font-size: 1rem;
  padding: 20px;
  opacity: 0.5;
  pointer-events: none;
  margin-bottom: 10px;
  
}

.tooltipContent{
  text-align: center;
  top: 10%;
  position: relative;
  
}

.tooltip::before { /* 삼각형 툴팁 */
  content: ' ';
  position: absolute;
  top: 100%; 
  left: 50%;
  width: 0;
  height: 0;
  margin-left: -10px;
  border: solid transparent;
  border-color: rgba(51, 51, 51, 0);
  border-top-color: #333;
  border-width: 10px;
  pointer-events: none;
}

.card:hover .tooltip {
  opacity: 1;
  display: block;
}

.tooltip_body .tooltip {
  left: 50%;
  transform: translateX(-61%);
}

